.main {
    background-color: #EAE8E8;
    .quote__container {
        display: flex;
        flex-direction: column;
        .quote {
            width: 95%;
            margin: 0 auto;
            padding: 50px 0;
            font-family: 'open sans', sans-serif;
           @media(orientation:landscape) {
            width: $main-width;
           }
           @include respond-to('sm') {
            padding: 75px 0;
            width: $main-width/1.5;
          }
          @include respond-to('xl') {
            width: $main-width/2;
          }
            &__text {
           
                font-size: 24px;
                text-align: center;
                width: $main-width;
                margin: 0 auto;
    
            }
           &__author {
               padding: 10px 15px;
               text-align: right;
           }
            &__icon__image {
                width: 15%;
                padding: 0  $main-spacing;
                @media(orientation:landscape) {
                   width: 10%;
                 }
                 @include respond-to('sm') {
                    width: 10%;
                  }
            }
        }
    }

    .about {
      display: flex;
      flex-direction: column;
      font-family: $primary-font-family;
      background-color: $primary-color;
      background: linear-gradient(360deg, rgba(11,11,11,1) 7%, rgba(18,18,19,0.8435749299719888) 40%, rgba(19,18,18,0.46262254901960786) 79%),url(../images/background-main-sm.jpg);
      background-size: cover;

     padding:  $main-spacing 0;
     @include respond-to('sm') {
      background: linear-gradient(360deg, rgba(11,11,11,1) 7%, rgba(18,18,19,0.8435749299719888) 40%, rgba(19,18,18,0.46262254901960786) 79%),url(../images/background-main-md.jpg);
    }
    @include respond-to('xl') {
      background: linear-gradient(360deg, rgba(11,11,11,1) 7%, rgba(18,18,19,0.8435749299719888) 40%, rgba(19,18,18,0.6811099439775911) 79%),url(../images/background-main.jpg);
    }
      &__heading {
        align-self: start;
        width: 85%;
        margin: 0 auto;
        padding: $main-spacing $main-spacing /2;
        font-size: $base-font-size * 1.5;
        color: $light-text-color;
        letter-spacing: 1px;
        @media (orientation:landscape) {
          width: 60%;
        }
      }
      &__text {
        align-self: center;
        justify-self: center;
        width: $main-width;
        margin: $main-spacing*3 auto $main-spacing*5;
        background-color: $secondary-color;
        padding:$main-spacing $main-spacing /2;
        text-align: center;
        border-radius: 16px;
        line-height: 25px;
        border-bottom: 15px solid $primary-color;
        @media (orientation:landscape) {
          width: 60%;
          padding:$main-spacing;
        }
        @include respond-to('sm') {
          width: $main-width / 1.2;
        }
        @include respond-to('md') {
          width: $main-width / 1.5;
        }
        @include respond-to('xl') {
          width: $main-width / 2;
      }
    }}


   

  }